			  app.goLoginPageTimeOut()
			  return
			}
		
			uni.login({
			      success: function(res) {
			        // 获取登陆凭证换取openid等信息
			        let invitedId = uni.getStorageSync('invitedId')
			        let familyId = uni.getStorageSync('familyId')
			        // 后台请求登录参数
			        let params = {
			          name: userInfo.nickname,
			          sex: userInfo.gender,
			          code: res.code,
			          invitedId: invitedId,
			          familyId:familyId
			        }
			        postCode(params).then(res=>{
			          if(res.code==1){
			            //加入成功 删除familyId
			            uni.removeStorageSync('familyId')
			            uni.showToast({
			              title: '恭喜您成功加入家庭账户',
			              icon: 'none',
			            })
			
			            setTimeout(() => {
			              uni.navigateTo({
			                url: '/pages/ICCard/familyAccount/index',
			              })
			            }, 200);
			          }else{
			            uni.showToast({
			              title: res.message,
			            })
			          }
					  this.fInvite = false
			        })
			      }
			    })
		},
